<template>
  <div class="content">
    <!---->
    <div class="breadCrumb">
      <div class="standard">
        <div class="iconfont rz-iconditu float-left font-24"></div>
        <a class="float-left font-16 ml-25">-<span class="cl-888"></span>-<span
                class="cl-888"></span></a>
        <div class="iconfont rz-iconyou float-right font-24"></div>
        <a class="float-right font-16 mr-5" style="cursor: pointer;"></a>
      </div>
    </div>

    <div class="standard">
      <div style="text-align: right;color: #E6A23C;">WPSWPS.xlsx.xls</div>
      
      <div class="T-title">
        <div class="row-top-blue"></div>
        <span class="row-top-text"></span>
        <span class="color_3 font-16" id="ssq">{{company.month}}</span>
        <span class="color_2 font_1" id="kgxsj">({{deadDate}})</span>
        <span class="f-r excelFile" style="margin-top: -10px;">
          <input type="button" class="button button-blue" style="cursor: pointer;margin-right: 10px;" value="">
          <a type="file" class="button button-red f-r" style="border-radius: 15px;" @click="triggerClick"
             href="javascript:;"></a>
          <input type="file" ref="file" id="fileselect" style="display: none;">
          <download-excel
              class="f-r mr-10"
              :data="json_data"
              :fields="json_fields" 
              type="xlsx"
              name=".xlsx">
            <a class="button button-blue" href="javascript:;"></a>
          </download-excel>
        </span>
      </div>
      <div>
        <span class="pad15_r" id="createDate">
          <label>
            <input class="form-control datepicker" disabled style="width: 110px; display: inline;" type="text"
                   name="createStart"> -
            <input class="form-control datepicker" disabled style="width: 110px; display: inline;" type="text" name="createEnd">
          </label>
        </span>
        <span class="pad15_r" id="checkDate" style="display: none;">
          <label>
            <input class="form-control datepicker" disabled style="width: 110px; display: inline;" type="text" name="checkStart"> -
            <input class="form-control datepicker" disabled style="width: 110px; display: inline;" type="text" name="checkEnd">
          </label>
        </span>
        <span class="pad15_r"><input class="input_1" name="saler_tax_no" disabled style="width: 200px;" type="text"></span>
        <span class="pad15_r checkState">
          <label class="pad30_r"><input name="abc" disabled type="radio" checked></label>
          <label class="pad30_r"><input name="abc" disabled type="radio"> </label>
        </span>
        <span>
          
          <label>
            <input class="form-control datepicker" disabled style="width: 110px; display: inline;" type="text"
                   name="create_at"> -
            <input class="form-control datepicker" disabled style="width: 110px; display: inline;" type="text" name="createEnd">
          </label>
        </span>
      </div>
    </div>

    <div class="standard">
      <div class="T-title">
        <div class="row-top-blue"></div>
        <span class="row-top-text"></span>
      </div>
      <p class="text-center standard-title"></p>
      <el-table
              class="checkTable"
              :data="checkTableData"
              border
              style="width: 100%;">
        <el-table-column label=""></el-table-column>
        <el-table-column prop="number" label=""></el-table-column>
        <el-table-column prop="money" label="()"></el-table-column>
        <el-table-column prop="tax_money" label="()"></el-table-column>
        <el-table-column label="">
          <p class="text-center col-red hand" @click="cancelChecks"></p>
        </el-table-column>
      </el-table>
      <p class="text-center standard-title mt-20"></p>
      <el-table
              :span-method="objectSpanMethod"
              class="checkTable"
              :data="checkTableDataAll"
              border
              style="width: 100%; color: red;">
        <el-table-column prop="datatime" label=""></el-table-column>
        <el-table-column prop="check" label=""></el-table-column>
        <el-table-column prop="number" label=""></el-table-column>
        <el-table-column prop="money" label="()"></el-table-column>
        <el-table-column prop="tax_money" label="()"></el-table-column>
      </el-table>
    </div>
    <div class="upload-remark mt-20">
      <div class="tips standard" style="margin-top: 20px;">
        <div class="tips-con-bz">
          <div class="row-top-blue"></div>
          <span class="row-top-text"></span>
        </div>
        <p>1</p>
        <p>2</p>
        <p>3</p>
        <p>4</p>
        <p>5</p>
        <p>6</p>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import XLSX from "xlsx";
  export default {
    name: "Upload",
    data() {
      return {
        // 
        json_fields: {
          '': 'check_state', // 
          '': 'code', // 
          '': 'number', // 
          '': 'create_time', // 
          '': 'tax_money', // 
          '': 'declare_tax_money', // 
          '': 'saler_name', // 
          '': 'saler_tax_no', // 
          '': 'money', // 
          '': 'use', // 
        },
        json_data: [],
        json_meta: [
          [
            {
              " key ": " charset ",
              " value ": " utf- 8 "
            }
          ]
        ],
        uploadList: [], // 
        checkTableData: [], // 
        checkTableDataAll: [], // 

        // 
        spanArr: [],
        position: 0,
        checkInvoice: null, // 

        notCheckNumber: 0, // 
        notCheckMoney: 0, // 
        notCheckTax_money: 0, // 

        checkMoney: 0, // 
        checkTax_money: 0, // 

      }
    },
    computed: {
      company() {
        return this.$store.state.company
      },
      ...mapGetters(['deadDate'])
    },
    mounted() {
      this.getUnSelectInvoice()
      this.readFile()
    },
    methods: {
      getUnSelectInvoice() {
        const invoice = this.$store.state.invoice
        // 
        let attr = []
        invoice.forEach((res) => {
          // 
          if (parseInt(res.check_state) === 0) {
            attr.push({...res})
          }
        })
        attr.forEach((item) => {
          item.check_state = '' //  0: 1:
          item.use = '' // 
        })
        this.json_data = attr
      },
      triggerClick() {
        this.$refs.file.dispatchEvent(new MouseEvent('click'))
      },
      readFile() {
        let fileselect = document.querySelector('#fileselect')
        fileselect.addEventListener('change', function (e) {
          e.preventDefault()
          const file = e.target.files
          if (file.length <= 0) { // 
            return
          } else if (!/\.(xls|xlsx)$/.test(file[0].name.toLowerCase())) {
            this.$message({
              type: 'info',
              message: 'xlsxlsx'
            })
            return
          }
          let reader = new FileReader()
          if (typeof FileReader === 'undefined') {
            this.$message({
              type: 'info',
              message: 'FileReader'
            })
            return
          }
          reader.readAsBinaryString(file[0])
          reader.onload = (e) => {
            try {
              let data = e.target.result
              let workbook = XLSX.read(data, {type: 'binary'})
              let wsname = workbook.SheetNames[0] // 
              let ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname])
              this.uploadList = ws // 
              // 
              this.processUploadList()
              this.$message.success('')
              return false
            } // json
            catch (err) {
              console.log(err)
              return false
            }
          }
        }.bind(this))
      },
      // 
      processUploadList() {
        // 
        this.spanArr = []
        this.position = 0
        this.checkInvoice = [] // 

        this.notCheckNumber = 0 // 
        this.notCheckMoney = 0 // 
        this.notCheckTax_money = 0 // 

        this.checkMoney = 0 // 
        this.checkTax_money = 0 // 


        var attr = []
        var uploadList = this.uploadList // 
        var invoiceList = this.$store.state.invoice
        for (let i = 0; i < invoiceList.length; i++) {
          for (let k = 0; k < uploadList.length; k++) {
            if (+invoiceList[i].number === uploadList[k][''] && uploadList[k][''] === '' && parseInt(invoiceList[i].check_state) !== 1) {
              attr.push(invoiceList[i])
              this.checkMoney = (Number(this.checkMoney) + uploadList[k][''] * 1).toFixed(2)
              this.checkTax_money = (Number(this.checkTax_money) + uploadList[k][''] * 1).toFixed(2)
            }
          }
        }

        this.checkInvoice = attr
        if (this.checkInvoice.length > 0) {
          this.getUnSelectInvoice()
          // 
          this.$store.commit('checkInvoice', attr)
          this.json_data = []
          // 
          this.rowspan()
        }
      },
      // 
      rowspan() {
        // 
        this.checkTableData = [
          {
            datatime: this.company.month + '-30',
            datadate: this.company.month + '-30',
            number: this.checkInvoice.length,
            money: this.checkMoney,
            tax_money: this.checkTax_money
          }
        ]
        this.checkTableDataAll = [
          {
            datatime:  this.company.month + '-30',
            datadate:  this.company.month + '-30',
            check: '',
            number: this.checkInvoice.length,
            money: this.checkMoney,
            tax_money: this.checkTax_money
          },
          {
            datadate: this.company.month + '-30',
            check: '',
            number: this.notCheckNumber,
            money: this.notCheckMoney,
            tax_money: this.notCheckTax_money
          },
        ]
        this.checkTableDataAll.forEach((item, index) => {
          if (index === 0) {
            this.spanArr.push(1);
            this.position = 0;
          } else {

            if (this.checkTableDataAll[index].datadate === this.checkTableDataAll[index - 1].datadate) {
              this.spanArr[this.position] += 1;
              this.spanArr.push(0);
            } else {
              this.spanArr.push(1);
              this.position = index;
            }
          }
        })
      },
      // 
      objectSpanMethod({row, column, rowIndex, columnIndex}) {
        if (columnIndex === 0) {
          const _row = this.spanArr[rowIndex];
          const _col = _row > 0 ? 1 : 0;
          return {
            rowspan: _row,
            colspan: _col
          }
        }
      },
      // 
      cancelChecks() {
        let fileselect = document.querySelector('#fileselect')
        fileselect.value = null  // change
        this.json_data = this.checkInvoice
        this.spanArr = []
        this.uploadList = []
        this.$store.commit('unCheckInvoice', this.checkInvoice) // 
        this.checkInvoice = null
        this.reductionInvoiceState()
        this.$message.success('')
      },
      // 
      reductionInvoiceState() {
        this.checkInvoice = [], // 
        this.notCheckNumber = 0, // 
        this.notCheckMoney = 0, // 
        this.notCheckTax_money = 0, // 

        this.checkMoney = 0, // 
        this.checkTax_money = 0 // 

        this.checkTableData = []
        this.checkTableDataAll = []
      }
    }
  }
</script>

<style>
  .content .checkTable th {
    background-color: #56627F;
    color: #ffffff;
  }

  .content .checkTable .cell {
    text-align: center;
  }

  .content .gutter {
    display: block !important;
  }
</style>
<style scoped>
  .hand {
    cursor: pointer;
  }

  .col-red {
    color: red;
  }

  .upload-remark {
    border-top: 20px solid #F7F7F7;
  }

  .standard-title {
    font-size: 14px;
    padding: 10px 0;
    color: #666666;
    font-weight: 700;
  }

  .tips p {
    color: #888888;
    margin: 10px 0;
  }

  .tips-con-bz {
    height: 30px;
    border-bottom: 2px solid #f7f7f7;
  }

  .content {
    position: relative;
    margin: 0 auto;
  }

  .breadCrumb {
    padding-left: 50px;
    padding-right: 50px;
    height: 80px;
    line-height: 40px;
    border-bottom: 20px solid #F7F7F7;
  }
</style>



